// 1 = loud
var VOLUME = 0.3;
var tonlaenge = 0.5;
var sweepDifferenz = 0;

var octavmtpl = 1;


// work around typescript warnings
const wnd = window as any;

export const Notes: {
  [key: string]: number
} = {
  C: 261.63,
  Cis: 277.18,
  D: 293.66,
  Dis: 311.13,
  E: 329.63,
  F: 349.23,
  Fis: 369.99,
  G: 392,
  Gis: 415.30,
  A: 440,
  Ais: 466.16,
  B: 493.88,
  C2: 261.63 * 2
};

class Synthesizer {

  private audioCtx: AudioContext = new (wnd['AudioContext'] || wnd['webkitAudioContext'])();

  private gain1: GainNode[];

  private myGain: any;



  constructor() {
    this.gain1 = [];
    this.gain1.length = 15;
    for (let i = 0; i < 15; i++) {
      this.gain1[i] = this.audioCtx.createGain();
      this.gain1[i].connect(this.audioCtx.destination);
    }



    // lower volume
    for (let i = 0; i < 15; i++) {
      this.gain1[i].gain.value = VOLUME / 10;
    }
    //this.gainNode.gain.value = VOLUME;
    //this.gainNode.gain.setValueAtTime(0.3, this.audioCtx.currentTime);

    //this.gainNode.gain.exponentialRampToValueAtTime(0.1, this.audioCtx.currentTime + 0.5);

  }

  public getGainNode() {
    //var volume = VOLUME;
    return VOLUME;
  }

  public getsweepDifferenz() {
    return sweepDifferenz
  }

  public getTonLaenge() {
    return tonlaenge;
  }

  public setOctavmtpl(a: number) {
    octavmtpl = a;
    console.log(octavmtpl)
  }

  public volumePlus() {
    if (VOLUME < 1) {
      VOLUME = VOLUME + 0.01;
    } else {
      VOLUME = 1;
    }
    for (let i = 0; i < 15; i++) {
      this.gain1[i].gain.value = VOLUME / 6;
    }


  }

  public volumeMinus() {
    if (VOLUME > 0.01) {
      VOLUME = VOLUME - 0.01;
    } else {
      VOLUME = 0;
    }
    for (let i = 0; i < 15; i++) {
      this.gain1[i].gain.value = VOLUME / 6;
    }

  }

  public tonlängePlus() {
    tonlaenge = tonlaenge + 0.1;
  }

  public tonlängeMinus() {
    tonlaenge = tonlaenge - 0.1;
  }

  public sweepDifferenzPlus() {
    sweepDifferenz = sweepDifferenz + 100;
  }

  public sweepDifferenzMinus() {
    sweepDifferenz = sweepDifferenz - 100;
  }

  public play(note: number, type: any, i: number) {
    const oscillator = this.audioCtx.createOscillator();
    let sweep = note * octavmtpl + sweepDifferenz;
    if(sweep<1){
      sweep=1
    }

  console.log("sweep:"+sweep);


    oscillator.connect(this.gain1[i]);


    //Osci[i].connect(this.myGain);
    // default

    oscillator.type = type;
    oscillator.frequency.value = note * octavmtpl;


    this.gain1[i].gain.setValueAtTime(VOLUME / 10, this.audioCtx.currentTime);
    //this.gainNode.gain.exponentialRampToValueAtTime(VOLUME, this.audioCtx.currentTime +0.5);
    //this.gainNode.gain.exponentialRampToValueAtTime(0.0, this.audioCtx.currentTime+0.5);
    this.gain1[i].gain.linearRampToValueAtTime(0.0, this.audioCtx.currentTime + tonlaenge - 0.1);
    //this.gainNode.gain.setValueAtTime(0, this.audioCtx.currentTime + 1);
    //oscillator.frequency.setValueAtTime(1, this.audioCtx.currentTime + 2 );
    //oscillator.frequency.setValueAtTime(0, this.audioCtx.currentTime + 2 );
    oscillator.frequency.exponentialRampToValueAtTime(/*note*octavmtpl*/ sweep, this.audioCtx.currentTime + tonlaenge -0.1);
    oscillator.start();
    oscillator.stop(this.audioCtx.currentTime + tonlaenge);
    //Osci[i].start();
    //Osci[i].stop(this.audioCtx.currentTime + tonlaenge);
    console.log(i);

  }


}

const synthesizer = new Synthesizer();
export default synthesizer;